main {
  background-color: #f4f4f4;
  padding: 20px;
}

.box img {
  width: 100%;
  height: auto;
  display: block;
}

.retail_wrapper {
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  display: flex;
}

.box {
  box-sizing: border-box;
  flex-direction: column;
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .retail_wrapper {
    padding: 10px;
  }

  .box {
    width: calc(50% - 10px);
  }
}

@media screen and (min-width: 1200px) {
  .retail_wrapper {
    flex-direction: row;
  }
}

.container {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 0 auto;
  overflow: hidden;
  box-shadow: 0 0 10px #0000001a;
}

